
<template>
  <el-container>
    <el-header>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/play' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-header>
    <el-container>
      <el-aside width="35%" class="process">
        <p></p>
        <div class="singleProcess" v-for="(item, index) in arr">
          <p>{{item.label}}</p>
          <el-steps :active="item.active" finish-status="success">
            <el-step v-for="(i,j) in item.children" :title="i.step_name"  @click.native="detail(i.id)"></el-step>
          </el-steps>

          <el-button type="success" @click="next(index)"></el-button>
        </div>
      </el-aside>
      <el-main class="detail">
        <p v-if="!details.step_name"></p>
        <p>{{details.step_name}}</p>
        <video width="80%" height="350" controls v-if="videoUrl"  :src="videoUrl">
          <source type="video/mp4">
         video 
        </video>
        <el-carousel :interval="2000" type="card" height="200px" style="width: 80%;margin-top: 15px">
          <el-carousel-item v-for="(picture,index) in details.picUrl" :key="index">
            <img :src="picture" class="pic"></img>
          </el-carousel-item>
        </el-carousel>
        <p>{{details.info}}</p>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
import echarts from 'echarts'
export default{
  name:'show',
  data(){
    return {
      active: 0,
      arr: [],
      id: null,
      details: {},
      videoUrl: ''
    }
  },
  created() {
    let vm = this
    this.$http.get(
      '/pet/rolePlay/getRoleProcedure?role=' + this.$route.params.role
    ).then(function(res) {
      console.log(res.data)
      vm.arr = res.data.map((item, index) => {
        item.active = index == 0 ? 0 : null
        return item
      })
      if(vm.arr[0] && vm.arr[0].children && vm.arr[0].children[0]) {
        vm.id = vm.arr[0].children[0].id
      }
    }).catch(function(err) {})
  },
  mounted () {
    
  },
  methods: {
    next(index) {
      if (this.arr[index].active++ > (this.arr[index].children.length -1)) this.arr[index].active = 0;
      if (this.arr[index].active > 0) {
        this.id = this.arr[index].children[this.arr[index].active - 1].id
      }
      //console.log(this.arr[index].children[this.arr[index].active].id)
    },
    detail(id) {
      this.id = id
    }
  },
  watch: {
    id (newval, oldval) {
      let vm = this
      this.$http.get(
        '/pet/rolePlay/getProcedureDetail?procedureID=' + newval
      ).then(function(res) {
        vm.details = res.data
        vm.videoUrl = res.data.videoUrl ? res.data.videoUrl[0] : ''
      }).catch(function(err) {})
    }
  }
}
</script>
<style scoped>
.moxing {
  width: 760px;
  height: 580px;
}
.process{
  display: flex;
  flex-direction: column;
  align-items: center;

}
.singleProcess{
  width: 90%;
  padding: 20px 15px 20px 15px;
}
.detail {
  margin-left: 25px;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #fff;
}
.pic{
  width: 100%;
  height: auto;
}
</style>


