
<template>
  <el-container>
  	<el-header>
  		<el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
  	</el-header>
  	<el-container>
  		<el-aside class="logout" width="100%">
  			<img :src="imageUrl" class="imgs" alt="">
        <p>{{name}}</p>
  			<el-button type="primary" @click="logout"></el-button>
  		</el-aside>
  	</el-container>
  </el-container>
</template>

<script>
import utils from '../util.js'

export default{
  name:'login',
  data(){
    return{
    }
  },
  mounted () {

  },
  computed: {
    name () {
      return this.$store.state.user.name || ''
    },
  	imageUrl () {
  		if (this.$store.state.user.picture_url) {
  			return this.$store.state.user.picture_url
  		}
  		else
  			return ''
  	}
  },
  created () {
  	console.log(this.$store.state.user)
  },
  methods: {
  	logout () {
  		this.$store.commit("login", {name:'',picture_url:'',id:null});
    	utils.delCookie('username')
    	utils.delCookie('picture_url')
    	utils.delCookie('userid')
  	  this.$notify({
        title: '',
        message: ''
      });
      this.$router.push('/')
  	}
  }
}
</script>
<style scoped>
.login-bottom {
	width: 100%;
	margin-top: 30px;
	display: flex;
	flex-direction: row-reverse;
}
.imgs {
	width: 180px;
	height: auto;
  margin-bottom: 25px;
}
.logout{
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>


