<template>
  <div id="wrapper">
      <div id="pano"></div>
  </div>
</template>
<script>
  export default {
    name:'vtour',
    data(){
      return {
          show:false
      }
    },
    computed: {
      isMobile () {
        return document.body.clientWidth < 500
      },
      notMobile () {
        return document.body.clientWidth >= 500
      }
    },
    mounted(){
      if (this.isMobile) {
        this.$router.push('/show')
      }
      embedpano({swf:"/dist/static/vtour/krpano.swf", xml:"/dist/static/vtour/walkthrough.xml", target:"pano", html5:"prefer", mobilescale:1.0, passQueryParameters:true});
    },
    methods:{
      getorder(msg){
          this.show = msg
      }
    },
    components:{

    }
  }
</script>
<style scoped>
#wrapper{
    width: 100%;
    height: 100%;
    position: absolute;
}
#pano{
    width: 100%;
    height: 100%;
}
.img_btn{
    width:60px;
    height: 60px;
    position: absolute;
    top:10px;
    right:6px;
    z-index:4010;
}
.showUp{
    animation:showup 0.4s ease-in;
}
.hideDown{
    animation:hidedown 0.4s ease-out;
}

@keyframes showup {
    from {
        transform:translateY(110%);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
    
}
@keyframes hidedown {
    from {
        transform: translateY(0);
    }
    to {
        transform: translateY(110%);
    }
    
}
</style>




