
<template>
  <el-container>
  	<el-header>
  		<el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
  	</el-header>
  	<el-container>
  		<el-aside width="70%" v-if="notMobile">
        <img :src="bg" style="width: 100%; height: auto;">
      </el-aside>
      <el-main style="padding-top: 0">
        <el-card class="box-card" v-if="register" v-loading="loading1"
			    element-loading-text=""
			    element-loading-spinner="el-icon-loading"
			    @keyup.enter.native="login">
				  <div slot="header" class="clearfix">
				    <span></span>
				    <el-button style="float: right; padding: 3px 0" type="text" @click="goRegister">,</el-button>
				  </div>
				  <div>
				  	<div class="demo-input-suffix" style="display: flex;">
						  <span style="min-width: 60px;line-height: 40px">:</span>
						  <el-input
						    placeholder=""
						    v-model="account">
						  </el-input>
						</div>
						<div class="demo-input-suffix" style="display: flex;margin-top: 20px">
						  <span style="min-width: 60px;line-height: 40px">:</span>
						  <el-input
						    placeholder=""
						    type="password"
						    v-model="password"
						    >
						  </el-input>
						</div>
						<div class="login-bottom">
							<el-button type="primary" @click="login"></el-button>
						</div>
				  </div>
				</el-card>

				<el-card class="box-card" v-if="!register" v-loading="loading1"
			    element-loading-text=""
			    element-loading-spinner="el-icon-loading">
				  <div slot="header" class="clearfix">
				    <span></span>
				    <el-button style="float: right; padding: 3px 0" type="text" @click="goLogin">,</el-button>
				  </div>
				  <div>
				  	<div style="margin-bottom: 15px;">
				  		<span></span>
				  		<el-upload
							  class="avatar-uploader"
							  action="http://www.ecnupet.cn:8080/pet/file"
							  :show-file-list="false"
							  :on-success="handleAvatarSuccess"
							  :before-upload="beforeAvatarUpload">
							  <img v-if="imageUrl" :src="imageUrl" class="avatar">
							  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
							</el-upload>
				  	</div>
				  	<div class="demo-input-suffix" style="display: flex;">
						  <span style="min-width: 60px;line-height: 40px">:</span>
						  <el-input
						    placeholder=""
						    v-model="account1">
						  </el-input>
						</div>
						<div class="demo-input-suffix" style="display: flex;margin-top: 20px">
						  <span style="min-width: 60px;line-height: 40px">:</span>
						  <el-input
						    placeholder=""
						    type="password"
						    v-model="password1">
						  </el-input>
						</div>

						<div class="demo-input-suffix" style="display: flex;margin-top: 20px">
						  <span style="min-width: 90px;line-height: 40px">:</span>
						  <el-input
						    placeholder=""
						    type="password"
						    v-model="repassword">
						  </el-input>
						</div>
						<div class="login-bottom">
							<el-button type="primary" @click="regis"></el-button>

						</div>
				  </div>
				</el-card>
      </el-main>
  	</el-container>
  </el-container>
</template>
<script>
import utils from '../util.js'
import crypto from 'crypto'

export default{
  name:'login',
  data(){
    return{
    	register: true,
    	account: '',
    	password: '',
    	account1: '',
    	password1: '',
    	repassword: '',
    	loading1: false,
    	image: '',
    	bg: require('../assets/imgs/time.jpg')
    }
  },
  computed: {
  	imageUrl () {
  		if (this.image)
  			return  'http://www.ecnupet.cn:8080/pet/' + this.image
  		else
  			return ''
  	},
  	isMobile () {
      return document.body.clientWidth < 500
    },
    notMobile () {
      return document.body.clientWidth >= 500
    }
  },
  created () {
  },
  methods: {
  	goRegister () {
  		this.register = false
  	},
  	goLogin () {
  		this.register = true
  	},
  	regis () {
  		let vm = this
  		if (!vm.account1 || !vm.password1) {
  			vm.$notify({
          title: '',
          message: '~'
        });
        return false
  		}
  		vm.loading1 = true
      let md5 = crypto.createHash("md5")
      md5.update(vm.password1)
      
  		this.$http.post('/pet/user/register',{
  			name: vm.account1,
  			pwd: md5.digest('hex'),
  			auth: 1,
  			picture_url: vm.image

  		},{
  			'Access-Control-Allow-Origin': '*'
  		})
	    .then(function(res){


	    	vm.loading1 = false
	    	vm.$router.push('/')
	    })
	    .catch(function(err){
	        console.log(err);
	        vm.loading1 = false
	        vm.$notify({
          title: '',
          message: '~'
        });
	    })
  	},
  	login () {
  		let vm = this
  		if (!vm.account || !vm.password) {
  			vm.$notify({
          title: '',
          message: '~'
        });
        return false
  		}
  		vm.loading1 = true
  		let md5 = crypto.createHash("md5")
      md5.update(vm.password)
  		this.$http.post(
  			'/pet/user/login',
	  		{
	  			username: vm.account,
	  			password: md5.digest('hex')
	  		},
	  		{
	  			'Access-Control-Allow-Origin': '*'
	  		}
	  	).then(function(res) {
	    	vm.$store.commit("login", {name:res.data.user.name,picture_url:res.data.user.picture_url,id:res.data.user.id});
	    	utils.setCookie('username', res.data.user.name)
	    	utils.setCookie('picture_url',res.data.user.picture_url)
	    	utils.setCookie('userid',res.data.user.id)
	    	vm.loading1 = false
	    	vm.$router.push('/')
	    }).catch(function(err) {
	        console.log(err);
	        vm.loading1 = false
	        vm.$notify({
          title: '',
          message: '~'
        });
	    })
  	},
  	handleAvatarSuccess(res, file) {
      this.image = res.webURL
      console.log(res.webURL)
    },
    beforeAvatarUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 4;
      const isJPG = (file.type === 'image/jpeg') || (file.type === 'image/png') || (file.type === 'image/jpg') || (file.type === 'image/bmp') ||  (file.type === 'image/gif');
      if (!isJPG) {
        this.$message.error('');
      }
      if (!isLt2M) {
        this.$message.error(' 4MB!');
      }
      return isLt2M;
    }
  }
}
</script>
<style scoped>
.login-bottom {
	width: 100%;
	margin-top: 30px;
	display: flex;
	flex-direction: row-reverse;
}
.avatar-uploader {
	display: inline-block;
	margin-left: 20px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: auto;
  display: block;
}
</style>


