//index.vue
<template>
  <div class="bg">
    <img :src="bgpic" class="bgpic"></img>
    <div :class="{ 'bgtxtMobile': isMobile, 'bgtxt': notMobile}">
      <div :class="{ 'singleMobile': isMobile, 'single': notMobile}" v-for="(item, index) in carou">
        <div style=" font-family: 'PingFang SC';color: #026660;font-size: 26px">{{item.title}}</div>
        <div>{{item.desc}}</div>
        <el-button type="primary" @click="open(item.open)">{{item.btnText}}</el-button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'home',
  data () {
    return {
       carou: [
        {
          title: '',
          desc: '3D',
          bg: '', // 
          open: '2', // 
          btnText: '' //
        },
        {
          title: '',
          desc: ',',
          bg: '', // 
          open: '3-1', // 
          btnText: '' //
        },
        {
          title: '',
          desc: '',
          bg: '', // 
          open: '3-2', // 
          btnText: '' //
        },
        {
          title: '',
          desc: '',
          bg: '', // 
          open: '4', // 
          btnText: '' //
        }
       ]
    }
  },
  methods:{
    open (key) {
      switch(key){
        case '2':
          this.$router.push('/show')
          break
        case '3-1':
          if (!this.$store.state.user.name) {
            this.$alert('', '', {
              confirmButtonText: '',
              callback: action => {
                this.$router.push('/login')
              }
            });
          }
          else {
            this.$router.push('/play')
          }
          break
        case '3-2':
          if (!this.$store.state.user.name) {
            this.$alert('', '', {
              confirmButtonText: '',
              callback: action => {
                this.$router.push('/login')
              }
            });
          }
          else {
            this.$router.push('/learn')
          }
          break
        case '4':
          if (!this.$store.state.user.name) {
            this.$alert('', '', {
              confirmButtonText: '',
              callback: action => {
                this.$router.push('/login')
              }
            });
          }
          else {
            this.$router.push('/test')
          }
          break
        default:
          this.$router.push('/')
      }
    }
  },
  computed: {
    isMobile () {
      return document.body.clientWidth < 500
    },
    notMobile () {
      return document.body.clientWidth >= 500
    },
    bgpic () {
      return this.isMobile ? require('../assets/imgs/back2.jpg') : require('../assets/imgs/back.jpg')
    }
  }
}
</script>

<style scoped>
@import url("//unpkg.com/element-ui@2.2.2/lib/theme-chalk/index.css");
.bg {
  position: relative;
  width: 95%;
  padding-left: 2.5%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: visible;
}
.bgpic {
  width: 100%;
  height: auto;
}
.bgtxtMobile{
  width: 100%;
  height: auto;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
.bgtxt{
  width: 100%;
  height: auto;
  flex-grow: 1;
  margin-top: -20px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.singleMobile{
  width: auto;
  height: auto;
  background: #fff;
  display: flex;
  margin-top: 15px;
  padding: 10px 15px 10px 15px;
  justify-content: space-around;
  align-items: center;
  background: #fff;
}
.single{
  width: auto;
  height: 200px;
  background: #fff;
  border-top: 5px solid #026660;
  display: flex;
  margin: 0 20px 0 20px;
  padding: 0 20px 0 20px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
/*  .el-carousel__item{
    border-bottom: 1px #fff solid;
  }*/ 
  .el-carousel__item:nth-child(2n) {
    background-color: #17B794;
    color: #fff;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #17B794;
    color: #fff;
  }
.singleCarousel{
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  -webkit-display: flex;
  flex-direction: column;
  -webkit-flex-direction: column;
  align-items: center;
  justify-content: center;
}
.singleCarousel div{
  text-align: center;
}
.singleCarousel .title, .singleCarousel .desc{
  font-size: 40px;
  height: 60px;
  line-height: 60px;
}
.singleCarousel .desc{
  font-size: 28px;
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin-top: 35px;
}
.singleCarousel .btn{
  margin-top: 100px;
  padding: 6px 30px 6px 30px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  border: 1px #fff solid;
  font-size: 30px;
  border-radius: 5px;
}
.singleCarousel .btn:hover{
  color: #000;
  background: #fff;
}
</style>




