
<template>
  <el-container>
  	<el-header>
  		<el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/learn' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
  	</el-header>
  	<el-container width="100%">
      <el-aside width="15%" class="left">
        <p v-if="!datas[0]"></p>
        <el-button type="primary" v-if="datas[0]" @click="nextStage" style="margin-bottom:20px"></el-button>
        <el-steps direction="vertical" :active="active" style="height:350px">
          <el-step v-for="(item,index) in datas" :title="item.name"></el-step>
        </el-steps>
      </el-aside>
      <el-main width="85%" style="padding:0;overflow:visible">
        <div class="stages">
          <div class="stage">
            <div class="main">
              <div class="t">
                  <el-card class="box-card" style="width: 25%;">
                    <div slot="header" class="clearfix">
                      <span></span>
                    </div>
                    <div class="texts" v-if="datas[active]">
                      <p class="desc" v-for="(word, i) in datas[active].text">{{word}}</p>
                    </div>
                    <div class="texts" v-else="datas[active]">
                      <p class="desc"></p>
                    </div>
                  </el-card>
                  <el-card class="box-card" style="width: 75%;">
                    <div slot="header" class="clearfix">
                      <span></span>
                    </div>
                    <div style="background: #fff;display: flex;flex-direction: column;align-items: center;">
                      <video width="80%" height="350" controls :src="curvideo">
                        <source type="video/mp4" v-if="curvideo">
                         video 
                      </video>
                      <el-button-group class="groups">
                        <el-button @click="last" type="primary" icon="el-icon-arrow-left"></el-button>
                        <el-button @click="next" type="primary"><i class="el-icon-arrow-right el-icon--right"></i></el-button>
                      </el-button-group>
                    </div>
                  </el-card>
              </div>
              <el-card class="box-card" style="width: 75%;margin-left: 25%">
                <div slot="header" class="clearfix">
                  <span></span>
                </div>
                <div class="photos" v-if="datas[active]">
                  <el-carousel :interval="2000" type="card" height="300px" style="width: 80%;">
                    <el-carousel-item v-for="(picture,index) in datas[active].picURL" :key="index">
                      <img :src="picture" class="pic"></img>
                    </el-carousel-item>
                  </el-carousel>
                </div>
                <div class="photos" v-else="datas[active]">
                  <p class="desc"></p>
                </div>
              </el-card>
            </div>
          </div>
        </div>
      </el-main>
  	</el-container>
  </el-container>
</template>
<script>
export default{
  name: 'case',
  data () {
    return{
      caseID: null,
      datas: [
        {
          name: '',
          text: [],
          videoURL: [],
          picURL: []
        }
      ],
      i: 0,
      active: 0
    }
  },
  computed: {
    curvideo () {
      console.log(this.datas[this.active])
      if (this.datas.length > 0 && this.datas[this.active].videoURL && this.datas[this.active].videoURL.length > 0)
        return this.datas[this.active].videoURL[this.i]
      else
        return ''
    }
  },
  created() {
    let vm = this
    vm.caseID = this.$route.params.case
    vm.$http.get(
      '/pet/case/getCaseDetail?caseID=' + vm.caseID
    ).then(function(res) {
      vm.datas = res.data.stages
      console.log(vm.datas)
    })
  },
  methods: {
    last () {
      console.log(this.datas)
      this.i = this.i == 0 ? (this.datas[this.active].videoURL.length - 1) : (this.i-1)

    },
    next () {
      this.i = this.i == (this.datas[this.active].videoURL.length - 1) ? 0 : (this.i + 1)
    },
    nextStage () {
      this.i = 0
      if (this.active == (this.datas.length - 1)){
        this.active = 0
      }
      else{
        this.active = this.active + 1
      }
    }
  }
}
</script>
<style scoped>
.zujian {
	width: 50%;
	height: 500px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}
.btn{
	align-self: flex-end;
	margin-top: 30px;
}
.pic{
  width: 100%;
  height: auto;
}
.detail {
  margin-left: 25px;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #fff;
}
.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.groups{
  width: 100%;
  display: flex;
  justify-content: center;
  height: auto;
  margin-top: 15px;
  margin-bottom: 15px;
}
.photos{
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  overflow: visible;
  background: #fff;
}
.desc{
  text-align: center;
}
.left{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.t{
  width: 100%;
  height: auto;
  display: flex;
}
</style>


