<!--App.vue-->
<template>
    <div id="app" class="bg">
      <el-container style="height: 100%">
        <el-header class="topbar">
          <el-row :gutter="20" justify="space-between" type="flex">
            <el-col :span="12">
              <div class="name">
                {{isMobile?'':''}}
              </div>
            </el-col>
            <el-col :span="12">
              <div class="grid-content">
                <el-menu v-if="isMobile" class="el-menu-demo" mode="horizontal" @select="handleSelect" text-color="#000" background-color="#F1F0EE"
    active-text-color="#026660" menu-trigger="click">
                  <el-submenu  index="5" class="subitem">
                    <template slot="title"></template>
                    <el-menu-item index="1"></el-menu-item>
                    <el-menu-item index="2-1"></el-menu-item>
                    <el-menu-item index="3-1"></el-menu-item>
                    <el-menu-item index="3-2"></el-menu-item>
                    <el-menu-item index="4"></el-menu-item>
                    <el-menu-item index="5">{{user}}</el-menu-item>
                  </el-submenu>
                </el-menu>
                <el-menu v-if="notMobile" class="el-menu-demo" mode="horizontal" @select="handleSelect" text-color="#000" background-color="#F1F0EE"
  active-text-color="#026660">
                  <el-menu-item class="subitem" index="1"></el-menu-item>
                  <el-submenu  index="2" class="subitem">
                    <template slot="title"></template>
                    <el-menu-item index="2-1"></el-menu-item>
                    <el-menu-item index="2-2">3D</el-menu-item>
                  </el-submenu>
                  <el-submenu  index="3" class="subitem">
                    <template slot="title"></template>
                    <el-menu-item index="3-1"></el-menu-item>
                    <el-menu-item index="3-2"></el-menu-item>
                  </el-submenu>
                  <el-menu-item class="subitem" index="4"></el-menu-item>
                  <el-menu-item class="subitem" index="5">{{user}}</el-menu-item>
                </el-menu>
              </div>
            </el-col>
          </el-row>
        </el-header>
        <el-main  style="padding: 0px;overflow: visible;">
          <div>
            <router-view></router-view>
          </div>
        </el-main>
      </el-container>  
    </div>
</template>

<script>
import utils from './util.js'
export default {
  name: 'app',
  data () {
    return {
    }
  },
  computed: {
    user () {
      return this.$store.state.user.name ? `${this.$store.state.user.name}` : ''
    },
     isMobile () {
      return document.body.clientWidth < 500
    },
    notMobile () {
      return document.body.clientWidth >= 500
    }
  },
  created () {
    let username = utils.getCookie('username')
    let picture_url = utils.getCookie('picture_url')
    let userid = utils.getCookie('userid')
    this.$store.commit("login", {name:username,picture_url:picture_url,id:userid});
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      switch(key){
        case '1':
          this.$router.push('/')
          break
        case '2-1':
          this.$router.push('/show')
          break
        case '2-2':
          this.$router.push('/3d')
          break
        case '3-1':
          if (!this.$store.state.user.name) {
            this.$alert('', '', {
              confirmButtonText: '',
              callback: action => {
                this.$router.push('/login')
              }
            });
          }
          else {
            this.$router.push('/play')
          }
          break
        case '3-2':
          if (!this.$store.state.user.name) {
            this.$alert('', '', {
              confirmButtonText: '',
              callback: action => {
                this.$router.push('/login')
              }
            });
          }
          else {
            this.$router.push('/learn')
          }
          break
        case '4':
          if (!this.$store.state.user.name) {
            this.$alert('', '', {
              confirmButtonText: '',
              callback: action => {
                this.$router.push('/login')
              }
            });
          }
          else {
            this.$router.push('/test')
          }
          break
        case '5':
          if (!this.$store.state.user.name) {
            this.$router.push('/login')
          }
          else {
            this.$router.push('/my')
          }
          break
        default:
          this.$router.push('/show')
      }
    }
  }
}
</script>

<style scoped>
@import url("//unpkg.com/element-ui@2.2.2/lib/theme-chalk/index.css");

.bg {
  margin: 0;
  height: 100%;
}
.name {
  font-size: 35px;
  height: 60px;
  line-height: 60px;
  font-family: "PingFang SC";
  color: #026660;
}
.el-submenu{
  list-style: none;
  padding: 0;
  height: auto;
  line-height: auto;

}
.el-menu--horizontal{
  border-bottom: none;
}
.grid-content{
  display: flex;
  flex-direction: row-reverse;
}
.el-radio.is-bordered.is-disabled {
  border-color: #409EFF;

}

.el-radio__input.is-disabled + span .el-radio__label{
  color: #409EFF !important;
}
.el-radio__input.is-disabled .el-radio__inner, .el-radio__input.is-disabled.is-checked .el-radio__inner{
  background-color: #409EFF;
}
</style>



